@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@500;700&family=Roboto:wght@400;700&display=swap);
*,*::before,*::after{transition:0.3s;box-sizing:border-box;font-family:"Roboto", sans-serif}html,body{height:100%;width:100%}body{margin:0;background-color:#efefef;color:#d3d3d3;background-color:#272b30;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}h1{font-family:"Montserrat"}h2{margin:50px auto}h3{font-weight:normal}input{background-color:transparent;border-color:transparent;font-size:1.5rem;color:#d3d3d3;padding:5px 20px;border:4px solid rgba(42,57,124,0.4);border-radius:100px}input:focus{border-color:#2a397c;outline:none}li{border-radius:5px}li p,li h3{margin:4px 0}p{font-size:1.2rem}ul.series{list-style:none;padding:10px;width:70%;margin:16px auto}ul.series li:nth-child(2n){background-color:rgba(42,57,124,0.2)}#logo{grid-area:logo;height:30%;margin:40px}#header{display:grid;grid-template-columns:1fr 4fr 1fr;grid-template-areas:"logo title .";height:10vh}#root{min-width:70%;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;margin:auto}.error{color:#cf585c;margin:20px auto}.success{color:#d3d3d3;margin:20px auto}.flex-center{-webkit-justify-content:center;justify-content:center;display:-webkit-flex;display:flex;-webkit-flex-wrap:wrap;flex-wrap:wrap}.row{display:-webkit-inline-flex;display:inline-flex;margin:.5rem auto}.col{padding:1rem;-webkit-flex-direction:column;flex-direction:column;width:100%;margin:auto}.title{text-align:center;font-size:350%;grid-area:title;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content}.bold{font-weight:700}.no-bold{font-weight:normal}.outline{border:1px solid black;border-radius:10px}.btn,.small-btn-primary{line-height:normal;line-height:initial;display:inline-block;padding:16px 35px;text-shadow:none;text-align:center;text-decoration:none;border:none;color:#d3d3d3;border-radius:100px;cursor:pointer}.btn:hover,.small-btn-primary:hover{color:#d3d3d3;text-decoration:none}.btn-container{position:relative;z-index:1;display:inline-block}.btn-primary,.small-btn-primary{outline:none;position:relative;font-size:1.5rem;background-color:rgba(42,57,124,0.4)}.btn-primary:hover::before,.small-btn-primary:hover::before{height:100%;-webkit-transform:scaleX(1);transform:scaleX(1)}.btn-primary::before,.small-btn-primary::before{content:"";position:absolute;z-index:-1;left:0;bottom:0;height:0;width:100%;border-radius:100px;background-color:#2a397c;-webkit-transform:scaleX(0.75);transform:scaleX(0.75)}.small-btn-primary{padding:8px 18px;font-size:1.2rem}.row-input input,.row-input button{margin:0 10px}.separator{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;text-align:center;color:#d3d3d3;width:70%;margin:100px auto;font-size:1.5rem}.separator::before,.separator::after{content:"";-webkit-flex:1 1;flex:1 1;border-bottom:2px solid #d3d3d3}.separator:not(:empty)::before{margin-right:0.25em}.separator:not(:empty)::after{margin-left:0.25em}.signed-in-list-item{display:grid;grid-template-columns:1fr 1fr 1fr 1fr;grid-column-gap:20px;-webkit-column-gap:20px;column-gap:20px;border-radius:5px;padding:0 10px;height:45px}.signed-in-list-item:hover:not(#header){background-color:rgba(42,57,124,0.4) !important}.signed-in-list-item *{margin:auto 0}.drop-down{position:absolute;transition:0s;padding:16px 0}.drop-down .autocomplete{color:rgba(211,211,211,0.5);font-size:1.5rem;margin:0;padding:0 24px;height:29px}.drop-down ul.drop-down-box{list-style:none;padding:0;background-color:#202225;padding:18px 8px;width:calc(100% + 8px);margin:24px 0;border-radius:18px}.drop-down ul.drop-down-box li p{padding:4px 16px;cursor:pointer;margin:0}.drop-down ul.drop-down-box li:hover{background-color:rgba(42,57,124,0.2)}.pos-relative{position:relative}.empty-container{display:-webkit-flex;display:flex;-webkit-flex-direction:row;flex-direction:row}div.loader{height:106px;width:106px;border:3px solid transparent;border-bottom-color:#be2026;border-radius:100%;margin:auto;position:relative;-webkit-animation:rotate360 infinite linear 1.5s;animation:rotate360 infinite linear 1.5s}div.loader::before{content:"";position:absolute;display:inline-block;margin:5px;height:90px;width:90px;border:3px solid transparent;border-bottom-color:#2a397c;border-radius:100%;-webkit-animation:rotate360 infinite linear 2.25s;animation:rotate360 infinite linear 2.25s}div.loader::after{content:"";position:absolute;display:inline-block;top:0px;margin:13px;height:74px;width:74px;border:3px solid transparent;border-bottom-color:#d3d3d3;border-radius:100%;-webkit-animation:rotate360 infinite linear 1.5s;animation:rotate360 infinite linear 1.5s}@-webkit-keyframes rotate360{from{-webkit-transform:rotate(0deg);transform:rotate(0deg)}to{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}@keyframes rotate360{from{-webkit-transform:rotate(0deg);transform:rotate(0deg)}to{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}input::-webkit-outer-spin-button,input::-webkit-inner-spin-button{-webkit-appearance:none;margin:0}input[type=number]{-moz-appearance:textfield}

